import React from 'react'
import { Link } from 'react-router-dom'
import md from '../markdown/md.js'
import {
	uwu,
	uwu2,
	owo,
	setUwuState
} from '../why.js'

const OFFLINE_DEV = false,
	MIZA_URL = (window.location.hostname==="localhost"?"http://27.33.133.250:9801":""),
	MIZA_USERNAME = "Miza",
	PREFIX = "~"


export default class AtlasPage extends React.Component {
	constructor(props) {
		super();
		this.state = {
			searchTerm: '',
			hasFocused: false,
			showNSFW: !!localStorage.showNSFW,
			uwu: localStorage.uwu === "true",
			Help: {}
		}
		if (!OFFLINE_DEV) {
			fetch(MIZA_URL + '/static/HELP.json').then(b => {
				b.json().then(e => {
					this.setState({
						Help: e
					});
				})
			})
		}
		setUwuState(this.state.uwu);
		this.searchRef = React.createRef();
		setTimeout(() => {
			this.searchRef && this.searchRef.current && !this.state.hasFocused && (() => {
				this.setState({
					hasFocused: true
				})
				this.searchRef.current.focus();
			})()
		}, 500)
	}
	search = (e) => {
		this.setState({
			searchTerm: e.target.value
		});
	}
	checkValues = (obj, checkstr) => {
		for (let key in obj) {
			if (typeof obj[key] === "object") {
				if (this.checkValues(obj[key], checkstr)) {
					return true;
				}
			}
			if (obj[key].toString().toLowerCase().includes(checkstr.toLowerCase())) {
				if (key === "usage" && checkstr === "?") {
					return false;
				}
				return true;
			}
		}
		return false;
	}
	prepStr = (str) => {
		// 8ball is _8ball in help for some reason, so /shrug
		return uwu2(str.replace(/‚ü®MIZA‚ü©/g, MIZA_USERNAME).replace(/‚ü®WEBSERVER‚ü©/g, MIZA_URL).replace("_8Ball", "8Ball"))
	}
	checkIsNsfw = (e) => {
		if (this.state.showNSFW) {
			return false;
		}
		if (typeof e === "string") {
			e = e.toLowerCase();
			return e === "nsfw";
		}
		if (typeof e === "object") {
			return (e.aliases && e.aliases.includes([
				'nsfw',
				'neko',
				'lewd'
			]))
		}
	
		return !this.state.showNSFW
	}
	render() {
		document.title = uwu("Miza Command Atlas")
		this.searchRef && this.searchRef.current && !this.state.hasFocused && (() => {
			this.setState({
				hasFocused: true
			})
			this.searchRef.current.focus();
		})()
		let navItems = [],
			fullItems = [],
			gaming = [
				"Real talk",
				"using placeholders",
				"let's go pog",
				"I have no idea",
				"why I created uwu mode",
				"it was 3am",
				"there was",
				"no",
				"reason for it",
				"just a thought came in",
				"hey",
				"what if you make uwu mode",
				"it'd be funny",
				"and my head was like",
				"I can't think of any coherent",
				"objections to that so I guess, sure",
				"the problem is",
				"i couldn't think any coherent thoughts _anyways_",
				"I was just retarded right then",
				"because it was 3am, right",
				"anyways",
				"this has been",
				"Real Talk Using Placeholders",
				"tee emm",
				"all rights reserved",
				"copyright 2021",
				"hucario \"big brain\" jones" // no my last name isn't jones lol
			],
			allStar=["Somebody once told me", "The world is gonna roll me", "I ain't the sharpest tool in the shed", "She was looking kinda dumb", "With her finger and her thumb", "In shape of an \"L\" on her forehead","Well the years start coming", "And they don't stop coming", "Fed to the rules and I hit the ground running", "Didn't make sense", "Not to live for fun", "Your brain gets smart but your head gets dumb", "So much to do", "So much to seeSo what's wrong with taking the back street", "You'll never know if you don't go", "You'll never shine if you don't glow", "Hey now", "You're an all star", "Get your game on", "Go playHey now", "You're a rock Star", "Get the show on", "Get paid", "And all that glitters is gold", "Only shooting stars break the mold", "It's a cool place", "And they say it gets colder", "You're bundled up now", "Wait 'til you get older", "But the meteor men", "Beg to differ", "Judging by the hole in the satellite picture", "The ice we skateIs getting pretty thin", "The waters getting warm", "So you might as well swim", "My world's on fireHow about yours", "That's the way I like it and I never get boredHey nowYou're an all star", "Get your game on", "Go play", "Hey now", "You're a rock star", "Get the show onGet paidAnd all that glitters is goldOnly shooting stars break the mold", "Hey now", "You're an all star", "Get your game on", "Go playHey now", "You're a rock starGet the show on", "Get paid", "And all that glitters is gold", "Only shooting stars", "Somebody once asked", "Could you spare some change for gas", "I need to get myself away from this place", "I said yepWhat a concept", "I could use", "A little fuel myselfAnd we could all use a little change", "Well the years start coming", "And they don't stop coming", "Fed to the rules and I hit the ground running.", "Didn't make sense", "Not to live for fun", "Your brain gets smart but your head gets dumb", "So much to do", "So much to see", "So what's wrong with taking the back streets", "You'll never know if you don't go", "You'll never shine if you don't glow", "Hey now", "You're an all star", "Get your game on", "Go play", "Hey now", "You're a rock star", "Get the show on", "Get paid", "And all that glitters is gold", "Only shooting stars break the mold", "And all that glitters is gold", "Only shooting stars break the mold"],
			allStarKey=0,
			keys = 0
		if (Object.keys(this.state.Help).length === 0) {
			for (let i = 0; i < gaming.length; i++) {
				if (i === 0 || Math.random()<0.1) {
					navItems.push(<li
						className="sidebarCategory censored"
						key={keys++}
					>{gaming[i]}</li>)
					fullItems.push(<div
						className="categoryHead"
						key={keys++}
						><h1
							className="censored"
						>{gaming[i]}</h1></div>)	
					continue;
				}
				navItems.push(<li
					className="censored"
					style={{
						animationDelay: (allStarKey*500) + "ms"
					}}
					key={keys++}
				>{gaming[i]}</li>);
				fullItems.push(<div
					className="command transIn"
					style={{
						animationDelay: (allStarKey*500) + "ms"
					}}
					key={keys++}
					><h2
						className="censored"
					>{gaming[i]}</h2><h3
						className="aliases censored"
					>{gaming[i]}</h3><div 
						className="markup censored">{
							uwu(gaming[i])
					}</div><div className="markup usage censored">{owo(uwu2("Usage:"))}<br />{PREFIX}{uwu2(allStar[allStarKey])}</div>
					</div>
				)
				allStarKey++;
			}
		} else {
			for (let key in this.state.Help) {
				if (!this.checkValues(this.state.Help[key], this.state.searchTerm) &&  !key.toLowerCase().includes(this.state.searchTerm.toLowerCase())) {
					continue;
				}
				if (this.checkIsNsfw(key)) {
					continue;
				}
				navItems.push(<li
					className="sidebarCategory"
					key={keys++}
					><a
					href={"#" + encodeURIComponent(key.toLowerCase())}
				>{key}</a></li>)
				fullItems.push(<div
					id={key.toLowerCase()}
					className="categoryHead"
					key={keys++}
					><h1><a
						href={"#" + encodeURIComponent(key.toLowerCase())}
					>{uwu(key)}</a></h1></div>)	
					
				for (let e in this.state.Help[key]) {
				
					if (e.toLowerCase() === MIZA_USERNAME.toLowerCase()) {
						// is the auto-generated wack one
						continue;
					}
					if (this.checkIsNsfw(this.state.Help[key][e])) {
						continue;
					}
					if (!this.checkValues(this.state.Help[key][e], this.state.searchTerm) && !key.toLowerCase().includes(this.state.searchTerm.toLowerCase())) {
						continue;				
					}
					navItems.push(<li
						key={keys++}><a
						href={"#" + encodeURIComponent(e.toLowerCase())}
						id={"nav-"+ encodeURIComponent(e.toLowerCase())}
						className={document.location.hash.substring(1) === e.toLowerCase()?'active':null}
					>{this.prepStr(e)}</a></li>);
					fullItems.push(<div
						className={"command" + (document.location.hash.substring(1) === e.toLowerCase()?' active':'')}
						id={encodeURIComponent(e.toLowerCase())}
						key={keys++}
						><h2><a
							href={"#" + encodeURIComponent(e.toLowerCase())}
						>{this.prepStr(e)}</a></h2><h3
							className="aliases"
						>{this.state.Help[key][e].aliases.join(', ')}</h3><div className="markup">{
							md(uwu(this.prepStr(this.state.Help[key][e].description)))
						}</div><div className="markup usage">{owo(uwu2("Usage:"))}<br />{PREFIX}{uwu2(e)} {md(uwu2(this.state.Help[key][e].usage))}</div><Link 
							to={"run?command="+encodeURIComponent(e.toLowerCase())}
							className="run"
							>‚ñ∫</Link>
						</div>
					
					)
				}
			}
		}
		
		return (<>
				<link rel="stylesheet" href="/mizatlas/page/atlas/atlas.css" type="text/css" />
				<nav className="sidebar">
					<ul>{navItems}</ul>
				</nav>
				<main>
					<h1>{uwu("Command atlas")}</h1>
					<h2>{uwu("Search by category or any command property.")}</h2><div
						className="labeledInput"><input 
						type="checkbox"
						id="showNSFW"
						checked={this.state.showNSFW}
						onChange={(e) => {
							this.setState({
								showNSFW: e.target.checked
							})
							localStorage.showNSFW = true
						}}
					/><label htmlFor="showNSFW">{uwu("Show NSFW")}</label></div><div
						className="labeledInput"><input 
						type="checkbox"
						id="uwuify"
						checked={this.state.uwu}
						onChange={(e) => {
							this.setState({
								uwu: e.target.checked
							})
							localStorage.uwu = e.target.checked
							setUwuState(e.target.checked);
						}}
					/><label htmlFor="uwuify">uwu</label></div>
					<Link
						to="."
						className="back"	
					>{uwu("Back to home")}</Link>
					<div className="searchHolder">
					<input 
						className="search"
						placeholder={uwu("Search commands...")}
						value={this.state.searchTerm}
						onChange={this.search}
						tabIndex="0"
						ref={this.searchRef}
					/></div>{fullItems}</main>
				</>)
	}
}
